"use client";

import { useEffect, useState, useRef } from "react";
import { getToken } from "../lib/auth";

export default function TradePage() {
    const [status, setStatus] = useState("CONNECTING");
    const [apiMsg, setApiMsg] = useState("");

    // marketBoard: { BTCUSDT: { price, ts }, ETHUSDT: { ... } }
    const [marketBoard, setMarketBoard] = useState({});
    const [filter, setFilter] = useState("");

    const [lastEvent, setLastEvent] = useState(null);
    const [lastUpdateTs, setLastUpdateTs] = useState(null);
    const [pinned, setPinned] = useState(() => new Set());

    // UI
    const [theme, setTheme] = useState("light"); // 'light' | 'dark'
    const [activeTab, setActiveTab] = useState("trades"); // positions | orders | trades
    const [selectedSymbol, setSelectedSymbol] = useState("BTCUSDT");
    const [side, setSide] = useState("BUY");
    const [orderType, setOrderType] = useState("MARKET");
    const [qty, setQty] = useState("0.01");

    const UI_FLUSH_MS = Number(process.env.NEXT_PUBLIC_MARKET_FLUSH_MS || 1500);
    const MAX_SYMBOLS = Number(process.env.NEXT_PUBLIC_MAX_SYMBOLS || 1000);

    // Cache of latest prices by symbol
    const latestBoardRef = useRef({});
    // Stable insertion order for the table
    const symbolOrderRef = useRef([]);
    const latestTsRef = useRef(null);

    const backendBaseUrl = (process.env.NEXT_PUBLIC_BACKEND_URL || "http://localhost:8080").replace(/\/$/, "");
    const wsBaseUrl = (process.env.NEXT_PUBLIC_WS_URL || "ws://localhost:8081").replace(/\/$/, "");

    useEffect(() => {
        const ws = new WebSocket(`${wsBaseUrl}/prices`);
        ws.onopen = () => setStatus("OPEN");
        ws.onclose = () => setStatus("CLOSED");
        ws.onerror = () => setStatus("ERROR");
        ws.onmessage = (e) => {
            try {
                const outer = JSON.parse(e.data);
                setLastEvent(outer);

                // We expect: { type: 'REDIS_EVENT', channel, message, ts }
                if (outer?.type !== "REDIS_EVENT") return;

                const channel = outer.channel;
                let inner;
                try {
                    inner = JSON.parse(outer.message);
                } catch {
                    // If producers ever send non-JSON, ignore for market board.
                    return;
                }

                // Price channel: either full board snapshots or single ticks
                if (channel === "events:price:update") {
                    if (inner?.type === "MARKET_BOARD" && Array.isArray(inner.data)) {
                        const ts = inner.ts || outer.ts || Date.now();

                        // Merge snapshot into our cache (do not replace)
                        const board = latestBoardRef.current || {};
                        const order = symbolOrderRef.current || [];
                        const seen = new Set(order);

                        for (const t of inner.data) {
                            const symbol = t?.symbol ? String(t.symbol).toUpperCase() : "";
                            if (!symbol) continue;
                            const price = Number(t.price);
                            if (!Number.isFinite(price)) continue;

                            // Add new symbols until cap; ignore beyond cap for now
                            if (!seen.has(symbol)) {
                                if (order.length >= MAX_SYMBOLS) continue;
                                order.push(symbol);
                                seen.add(symbol);
                            }

                            board[symbol] = { price, ts };
                        }

                        latestBoardRef.current = board;
                        symbolOrderRef.current = order;
                        latestTsRef.current = ts;
                        return;
                    }

                    if (inner?.type === "PRICE_UPDATE" && inner.symbol) {
                        const price = Number(inner.price);
                        if (!Number.isFinite(price)) return;

                        const sym = String(inner.symbol).toUpperCase();
                        const ts = inner.ts || outer.ts || Date.now();

                        const board = latestBoardRef.current || {};
                        const order = symbolOrderRef.current || [];

                        if (!board[sym]) {
                            if (order.length >= MAX_SYMBOLS) return;
                            order.push(sym);
                            symbolOrderRef.current = order;
                        }

                        board[sym] = { price, ts };
                        latestBoardRef.current = board;
                        latestTsRef.current = ts;
                        return;
                    }
                }

                // Order status channel: keep lastEvent visible in UI (no special rendering yet)
                // channel === 'events:order:status'
            } catch {
                // ignore parse errors
            }
        };
        return () => ws.close();
    }, []);

    useEffect(() => {
        try {
            const raw = localStorage.getItem("pinnedSymbols");
            if (!raw) return;
            const arr = JSON.parse(raw);
            if (Array.isArray(arr)) setPinned(new Set(arr.map((s) => String(s).toUpperCase())));
        } catch { }
    }, []);

    useEffect(() => {
        try {
            localStorage.setItem("pinnedSymbols", JSON.stringify(Array.from(pinned)));
        } catch { }
    }, [pinned]);

    useEffect(() => {
        try {
            const t = localStorage.getItem("theme");
            if (t === "dark" || t === "light") setTheme(t);
        } catch { }
    }, []);

    useEffect(() => {
        try {
            localStorage.setItem("theme", theme);
        } catch { }

        // Tailwind dark mode: toggle the `dark` class on <html>
        if (theme === "dark") document.documentElement.classList.add("dark");
        else document.documentElement.classList.remove("dark");
    }, [theme]);

    useEffect(() => {
        // Flush latestBoardRef to state every UI_FLUSH_MS
        const interval = setInterval(() => {
            setMarketBoard({ ...latestBoardRef.current });
            setLastUpdateTs(latestTsRef.current);
        }, UI_FLUSH_MS);
        return () => clearInterval(interval);
    }, []);

    function togglePin(sym) {
        const s = String(sym).toUpperCase();
        setPinned((prev) => {
            const next = new Set(prev);
            if (next.has(s)) next.delete(s);
            else next.add(s);
            return next;
        });
    }

    function toggleTheme() {
        setTheme((t) => (t === "dark" ? "light" : "dark"));
    }

    function formatPrice(p) {
        const n = Number(p);
        if (!Number.isFinite(n)) return "—";
        return n.toFixed(8).replace(/0+$/, "").replace(/\.$/, "");
    }

    async function placeOrder() {
        const token = getToken();
        if (!token) return setApiMsg("No token found. Login first.");

        setApiMsg("Placing order...");

        const res = await fetch(`${backendBaseUrl}/api/trading/orders`, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                "Authorization": `Bearer ${token}`
            },
            body: JSON.stringify({
                symbol: selectedSymbol,
                side,
                type: orderType,
                quantity: qty,
            })
        });

        const data = await res.json();
        if (!data.ok) return setApiMsg(data.error || "Order failed");

        setApiMsg(`Order submitted ✅ orderId=${data.orderId} status=${data.status}`);
    }

    const f = filter.trim().toLowerCase();

    // Keep table stable: insertion order + pinned coins on top
    const orderedSymbols = symbolOrderRef.current;

    const pinnedList = Array.from(pinned);
    const pinnedSet = pinned;

    const pinnedRows = pinnedList
        .map((sym) => [sym, marketBoard[sym]])
        .filter(([sym, v]) => v && (!f || sym.toLowerCase().includes(f)));

    const regularRows = orderedSymbols
        .filter((sym) => !pinnedSet.has(sym))
        .map((sym) => [sym, marketBoard[sym]])
        .filter(([sym, v]) => v && (!f || sym.toLowerCase().includes(f)));

    const rows = [...pinnedRows, ...regularRows];
    const shown = rows.slice(0, MAX_SYMBOLS);

    // Derived rows (pinned first)
    const totalSymbols = symbolOrderRef.current.length;

    const currentPrice = marketBoard[selectedSymbol]?.price;

    const mockPositions = [
        { symbol: "BTCUSDT", size: "0.05", entry: "65000", mark: "67000", uPnl: "+100", rPnl: "+0" },
        { symbol: "ETHUSDT", size: "0.8", entry: "3200", mark: "3150", uPnl: "-40", rPnl: "+20" },
        { symbol: "SOLUSDT", size: "5", entry: "180", mark: "186", uPnl: "+30", rPnl: "+0" },
    ];

    const mockOrders = [
        { id: "ord_1", symbol: "BTCUSDT", side: "BUY", type: "LIMIT", qty: "0.01", status: "NEW" },
        { id: "ord_2", symbol: "ETHUSDT", side: "SELL", type: "MARKET", qty: "0.2", status: "FILLED" },
        { id: "ord_3", symbol: "SOLUSDT", side: "BUY", type: "MARKET", qty: "1", status: "REJECTED" },
    ];

    return (
        <main className="min-h-screen bg-slate-50 text-slate-900 dark:bg-slate-950 dark:text-slate-100 w-screen">
            <div className="mx-auto">
                {/* Topbar */}
                <div className="flex items-center justify-between rounded-2xl border border-slate-200 bg-white p-4 shadow-sm dark:border-slate-800 dark:bg-slate-900">
                    <div className="flex items-center gap-3 font-extrabold tracking-wide">
                        <Image src="/logo.png" alt="Logo" width={32} height={32} />
                    </div>

                    <div className="flex items-center gap-3">
                        <div className="flex items-center gap-2 rounded-full border border-slate-200 bg-slate-50 px-3 py-2 text-sm dark:border-slate-800 dark:bg-slate-800">
                            <span className="h-2 w-2 rounded-full bg-emerald-400 shadow-[0_0_0_4px_rgba(52,211,153,0.15)]" />
                            <span className="font-semibold">Live trading</span>
                            <span className="text-slate-500 dark:text-slate-300">| symbols {totalSymbols}</span>
                        </div>

                        <button
                            onClick={toggleTheme}
                            title="Toggle theme"
                            className="grid h-10 w-10 place-items-center rounded-xl border border-slate-200 bg-white text-slate-900 hover:bg-slate-50 dark:border-slate-800 dark:bg-slate-900 dark:text-slate-100 dark:hover:bg-slate-800"
                        >
                            {theme === "dark" ? "☾" : "☀"}
                        </button>

                        <button
                            title="Settings (mock)"
                            className="grid h-10 w-10 place-items-center rounded-xl border border-slate-200 bg-white text-slate-900 hover:bg-slate-50 dark:border-slate-800 dark:bg-slate-900 dark:text-slate-100 dark:hover:bg-slate-800"
                        >
                            ⚙
                        </button>

                        <button
                            title="Account (mock)"
                            className="grid h-10 w-10 place-items-center rounded-xl border border-slate-200 bg-white text-slate-900 hover:bg-slate-50 dark:border-slate-800 dark:bg-slate-900 dark:text-slate-100 dark:hover:bg-slate-800"
                        >
                            ☺
                        </button>
                    </div>
                </div>

                {/* Layout */}
                <div className="mt-5 grid gap-4 lg:grid-cols-[360px_1fr]">
                    {/* Left: Order panel */}
                    <div className="rounded-2xl border border-slate-200 bg-white p-4 shadow-sm dark:border-slate-800 dark:bg-slate-900">
                        <div className="flex items-center justify-between">
                            <h3 className="text-lg font-bold">Order</h3>
                            <span className="text-xs text-slate-500 dark:text-slate-300">Spot Testnet</span>
                        </div>

                        {/* BUY/SELL segmented */}
                        <div className="mt-4 inline-flex overflow-hidden rounded-xl border border-slate-200 bg-slate-50 dark:border-slate-800 dark:bg-slate-800">
                            <button
                                className={`px-4 py-2 text-sm font-semibold ${side === "BUY" ? "bg-white text-slate-900 dark:bg-slate-900 dark:text-slate-100" : "text-slate-500 dark:text-slate-300"}`}
                                onClick={() => setSide("BUY")}
                            >
                                BUY
                            </button>
                            <button
                                className={`px-4 py-2 text-sm font-semibold ${side === "SELL" ? "bg-white text-slate-900 dark:bg-slate-900 dark:text-slate-100" : "text-slate-500 dark:text-slate-300"}`}
                                onClick={() => setSide("SELL")}
                            >
                                SELL
                            </button>
                        </div>

                        {/* Order type buttons */}
                        <div className="mt-3 flex flex-wrap gap-2">
                            {[
                                { label: "LIMIT", val: "LIMIT" },
                                { label: "MARKET", val: "MARKET" },
                                { label: "STOP", val: "STOP_MARKET" },
                            ].map((t) => {
                                const active = orderType === t.val;
                                return (
                                    <button
                                        key={t.val}
                                        onClick={() => setOrderType(t.val)}
                                        className={`h-9 rounded-xl border px-3 text-sm font-semibold ${active
                                            ? "border-slate-200 bg-slate-50 text-slate-900 dark:border-slate-700 dark:bg-slate-800 dark:text-slate-100"
                                            : "border-slate-200 bg-white text-slate-500 hover:bg-slate-50 dark:border-slate-800 dark:bg-slate-900 dark:text-slate-300 dark:hover:bg-slate-800"}`}
                                    >
                                        {t.label}
                                    </button>
                                );
                            })}
                        </div>

                        {/* Inputs */}
                        <div className="mt-4 space-y-3">
                            <div>
                                <label className="text-sm text-slate-500 dark:text-slate-300">Symbol</label>
                                <input
                                    className="mt-1 w-full rounded-xl border border-slate-200 bg-white px-3 py-2 text-sm outline-none focus:ring-2 focus:ring-slate-200 dark:border-slate-800 dark:bg-slate-900 dark:focus:ring-slate-700"
                                    value={selectedSymbol}
                                    onChange={(e) => setSelectedSymbol(e.target.value.toUpperCase())}
                                    placeholder="BTCUSDT"
                                />
                            </div>

                            <div>
                                <label className="text-sm text-slate-500 dark:text-slate-300">Quantity</label>
                                <input
                                    className="mt-1 w-full rounded-xl border border-slate-200 bg-white px-3 py-2 text-sm outline-none focus:ring-2 focus:ring-slate-200 dark:border-slate-800 dark:bg-slate-900 dark:focus:ring-slate-700"
                                    value={qty}
                                    onChange={(e) => setQty(e.target.value)}
                                    placeholder="0.01"
                                />
                            </div>
                        </div>

                        {/* Stats */}
                        <div className="mt-4 rounded-2xl border border-slate-200 bg-slate-50 p-3 dark:border-slate-800 dark:bg-slate-800">
                            <div className="flex items-center justify-between">
                                <span className="text-sm text-slate-500 dark:text-slate-300">Last price</span>
                                <span className="text-sm font-bold">{formatPrice(currentPrice)}</span>
                            </div>
                            <div className="mt-2 flex items-center justify-between">
                                <span className="text-sm text-slate-500 dark:text-slate-300">WS</span>
                                <span className="text-sm font-semibold">{status}</span>
                            </div>
                        </div>

                        {/* CTA */}
                        <button
                            onClick={placeOrder}
                            className="mt-4 w-full rounded-2xl border border-slate-200 bg-slate-900 px-4 py-3 text-sm font-bold text-slate-50 hover:bg-slate-800 dark:border-slate-800 dark:bg-slate-100 dark:text-slate-900 dark:hover:bg-white"
                        >
                            {side === "BUY" ? "Buy" : "Sell"} {selectedSymbol}
                        </button>

                        {apiMsg ? <p className="mt-3 text-sm text-slate-500 dark:text-slate-300">{apiMsg}</p> : null}
                    </div>

                    {/* Right: chart placeholder + tabs */}
                    <div className="space-y-4">
                        {/* Chart */}
                        <div className="rounded-2xl border border-slate-200 bg-white p-4 shadow-sm dark:border-slate-800 dark:bg-slate-900">
                            <div className="flex items-start justify-between gap-4">
                                <div>
                                    <h3 className="text-lg font-bold">{selectedSymbol.replace("USDT", "/USDT")}</h3>
                                    <div className="mt-1 flex items-center gap-3">
                                        <div className="text-3xl font-extrabold">{formatPrice(currentPrice)}</div>
                                        <span className="rounded-full bg-emerald-50 px-3 py-1 text-xs font-extrabold text-emerald-700 dark:bg-emerald-950 dark:text-emerald-300">Live</span>
                                    </div>
                                    <p className="mt-2 text-sm text-slate-500 dark:text-slate-300">Chart is a placeholder for now. Next step: candlesticks + timeframe.</p>
                                </div>

                                <div className="inline-flex rounded-xl border border-slate-200 bg-slate-50 p-1 text-sm dark:border-slate-800 dark:bg-slate-800">
                                    {[
                                        { label: "1m" },
                                        { label: "5m" },
                                        { label: "1D" },
                                        { label: "1W" },
                                    ].map((t) => (
                                        <button
                                            key={t.label}
                                            className={`rounded-lg px-3 py-2 font-semibold ${t.label === "1D" ? "bg-white text-slate-900 dark:bg-slate-900 dark:text-slate-100" : "text-slate-500 dark:text-slate-300"}`}
                                            title="Mock timeframe"
                                        >
                                            {t.label}
                                        </button>
                                    ))}
                                </div>
                            </div>

                            <div className="mt-4 grid h-64 place-items-center rounded-2xl border border-dashed border-slate-300 bg-slate-50 text-slate-500 dark:border-slate-700 dark:bg-slate-800 dark:text-slate-300">
                                <div className="text-center">
                                    <div className="text-3xl">▥</div>
                                    <div className="mt-1 text-sm">Chart goes here</div>
                                </div>
                            </div>
                        </div>

                        {/* Tabs */}
                        <div className="rounded-2xl border border-slate-200 bg-white p-4 shadow-sm dark:border-slate-800 dark:bg-slate-900">
                            <div className="flex flex-wrap items-center justify-between gap-3">
                                <div className="inline-flex rounded-xl border border-slate-200 bg-slate-50 p-1 text-sm dark:border-slate-800 dark:bg-slate-800">
                                    {[
                                        { id: "positions", label: "Positions" },
                                        { id: "orders", label: "Orders" },
                                        { id: "trades", label: "Trades" },
                                    ].map((t) => (
                                        <button
                                            key={t.id}
                                            onClick={() => setActiveTab(t.id)}
                                            className={`rounded-lg px-3 py-2 font-semibold ${activeTab === t.id ? "bg-white text-slate-900 dark:bg-slate-900 dark:text-slate-100" : "text-slate-500 dark:text-slate-300"}`}
                                        >
                                            {t.label}
                                        </button>
                                    ))}
                                </div>

                                {activeTab === "trades" ? (
                                    <input
                                        className="w-64 rounded-xl border border-slate-200 bg-white px-3 py-2 text-sm outline-none focus:ring-2 focus:ring-slate-200 dark:border-slate-800 dark:bg-slate-900 dark:focus:ring-slate-700"
                                        value={filter}
                                        onChange={(e) => setFilter(e.target.value)}
                                        placeholder="Search symbols"
                                    />
                                ) : (
                                    <div />
                                )}
                            </div>

                            {/* Positions */}
                            {activeTab === "positions" ? (
                                <div className="mt-4 max-h-[48vh] overflow-auto rounded-2xl border border-slate-200 dark:border-slate-800">
                                    <table className="w-full border-collapse text-sm">
                                        <thead className="sticky top-0 bg-white text-slate-500 dark:bg-slate-900 dark:text-slate-300">
                                            <tr>
                                                <th className="px-4 py-3 text-left font-bold">Symbol</th>
                                                <th className="px-4 py-3 text-left font-bold">Size</th>
                                                <th className="px-4 py-3 text-left font-bold">Entry</th>
                                                <th className="px-4 py-3 text-left font-bold">Mark</th>
                                                <th className="px-4 py-3 text-left font-bold">Unrealized</th>
                                                <th className="px-4 py-3 text-left font-bold">Realized</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {mockPositions.map((p) => (
                                                <tr key={p.symbol} className="border-t border-slate-100 hover:bg-slate-50 dark:border-slate-800 dark:hover:bg-slate-800">
                                                    <td className="px-4 py-3 font-extrabold cursor-pointer" onClick={() => setSelectedSymbol(p.symbol)}>{p.symbol}</td>
                                                    <td className="px-4 py-3">{p.size}</td>
                                                    <td className="px-4 py-3">{p.entry}</td>
                                                    <td className="px-4 py-3">{p.mark}</td>
                                                    <td className="px-4 py-3">{p.uPnl}</td>
                                                    <td className="px-4 py-3">{p.rPnl}</td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            ) : null}

                            {/* Orders */}
                            {activeTab === "orders" ? (
                                <div className="mt-4 max-h-[48vh] overflow-auto rounded-2xl border border-slate-200 dark:border-slate-800">
                                    <table className="w-full border-collapse text-sm">
                                        <thead className="sticky top-0 bg-white text-slate-500 dark:bg-slate-900 dark:text-slate-300">
                                            <tr>
                                                <th className="px-4 py-3 text-left font-bold">ID</th>
                                                <th className="px-4 py-3 text-left font-bold">Symbol</th>
                                                <th className="px-4 py-3 text-left font-bold">Side</th>
                                                <th className="px-4 py-3 text-left font-bold">Type</th>
                                                <th className="px-4 py-3 text-left font-bold">Qty</th>
                                                <th className="px-4 py-3 text-left font-bold">Status</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {mockOrders.map((o) => (
                                                <tr key={o.id} className="border-t border-slate-100 hover:bg-slate-50 dark:border-slate-800 dark:hover:bg-slate-800">
                                                    <td className="px-4 py-3 text-slate-500 dark:text-slate-300">{o.id}</td>
                                                    <td className="px-4 py-3 font-extrabold cursor-pointer" onClick={() => setSelectedSymbol(o.symbol)}>{o.symbol}</td>
                                                    <td className="px-4 py-3">{o.side}</td>
                                                    <td className="px-4 py-3">{o.type}</td>
                                                    <td className="px-4 py-3">{o.qty}</td>
                                                    <td className="px-4 py-3">{o.status}</td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            ) : null}

                            {/* Trades (live board) */}
                            {activeTab === "trades" ? (
                                <div className="mt-4">
                                    <div className="mb-3 flex items-center justify-between text-sm text-slate-500 dark:text-slate-300">
                                        <div>Last update: <span className="font-bold text-slate-900 dark:text-slate-100">{lastUpdateTs ? new Date(lastUpdateTs).toLocaleTimeString() : "—"}</span></div>
                                        <div>Pinned: <span className="font-bold text-slate-900 dark:text-slate-100">{pinned.size}</span></div>
                                    </div>

                                    <div className="h-[70vh] overflow-auto rounded-2xl border border-slate-200 dark:border-slate-800">
                                        <table className="w-full border-collapse text-sm">
                                            <thead className="sticky top-0 bg-white text-slate-500 dark:bg-slate-900 dark:text-slate-300">
                                                <tr>
                                                    <th className="px-4 py-3 text-left font-bold w-20">Pin</th>
                                                    <th className="px-4 py-3 text-left font-bold">Symbol</th>
                                                    <th className="px-4 py-3 text-left font-bold">Last</th>
                                                    <th className="px-4 py-3 text-left font-bold">Updated</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {shown.map(([sym, v]) => (
                                                    <tr key={sym} className="border-t border-slate-100 hover:bg-slate-50 dark:border-slate-800 dark:hover:bg-slate-800">
                                                        <td className="px-4 py-3">
                                                            <button
                                                                onClick={() => togglePin(sym)}
                                                                title={pinned.has(sym) ? "Unpin" : "Pin"}
                                                                className="h-9 w-9 rounded-xl border border-slate-200 bg-white hover:bg-slate-50 dark:border-slate-800 dark:bg-slate-900 dark:hover:bg-slate-800"
                                                            >
                                                                {pinned.has(sym) ? "★" : "☆"}
                                                            </button>
                                                        </td>
                                                        <td className="px-4 py-3 font-extrabold cursor-pointer" onClick={() => setSelectedSymbol(sym)}>{sym}</td>
                                                        <td className="px-4 py-3">{formatPrice(v.price)}</td>
                                                        <td className="px-4 py-3 text-slate-500 dark:text-slate-300">{v.ts ? new Date(v.ts).toLocaleTimeString() : "—"}</td>
                                                    </tr>
                                                ))}

                                                {shown.length === 0 ? (
                                                    <tr>
                                                        <td colSpan={4} className="px-4 py-4 text-slate-500 dark:text-slate-300">Waiting for prices…</td>
                                                    </tr>
                                                ) : null}
                                            </tbody>
                                        </table>
                                    </div>

                                    <p className="mt-3 text-sm text-slate-500 dark:text-slate-300">
                                        Showing {shown.length} of {rows.length} (cap {MAX_SYMBOLS}). Click a symbol to load it into the order panel.
                                    </p>
                                </div>
                            ) : null}

                            <details className="mt-4">
                                <summary className="cursor-pointer text-sm text-slate-500 dark:text-slate-300">Debug: last WS envelope</summary>
                                <pre className="mt-3 overflow-x-auto rounded-2xl border border-slate-200 bg-slate-50 p-3 text-xs text-slate-800 dark:border-slate-800 dark:bg-slate-800 dark:text-slate-100">
                                    {lastEvent ? JSON.stringify(lastEvent, null, 2) : "Waiting..."}
                                </pre>
                            </details>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    );
}